import { useQuery } from "react-query";
import axios from "axios";
import { useStores } from "../state/Context";

export const useFetchHashtags = (id = "") => {
  const { HashtagStore, DataStore } = useStores();
  const { refetch } = useQuery(
    ["hashtags", id],
    () => axios.get(`http://${process.env.REACT_APP_HOST_URL}/hashtags/${id}`),
    {
      enabled: false,
      onSuccess: (data) => {
        console.log(`fetch${id}`);
        console.log(data.data);
        HashtagStore.setHashtags(data.data);
      },
      onError: (data) => {
        if (data.response.data.error === "not loggedIn") {
          DataStore.setLoggedin(false);
        }
      },
    }
  );
  return refetch;
};
