import { action, makeObservable, observable } from "mobx";

export class HashtagStore {
  rootStore;
  // user = {};
  Hashtags = [];

  constructor(root) {
    makeObservable(this, {
      Hashtags: observable,
      setHashtags: action,
    });

    this.rootStore = root;
    this.Tasks = [];
  }
  setHashtags(value) {
    if (value === undefined || value === null) {
      this.Hashtags = [];
    } else {
      this.Hashtags = value;
    }
  }
  length() {
    return this.Hashtags.length;
  }
}
