import { action, makeObservable, observable, runInAction } from "mobx";

export class TaskStore {
  rootStore;
  // user = {};
  Tasks = [];
  totalTask = 1;
  tasksInPage = 4;
  workingPage = 1;
  constructor(root) {
    makeObservable(this, {
      Tasks: observable,
      totalTask: observable,
      tasksInPage: observable,
      workingPage: observable,

      setWorkingPage: action,
      setTasks: action,
      setTotalTask: action,
      setTasksInPage: action,
    });

    this.rootStore = root;
    this.Tasks = [];
  }
  setTasks(value) {
    runInAction(() => {
      if (value === undefined || value === null) {
        this.Task = [];
      } else {
        this.Tasks = value;
      }
    });
  }
  length() {
    return this.Tasks.length;
  }
  setTotalTask(value) {
    this.totalTask = value;
  }
  setTasksInPage(value) {
    this.tasksInPage = value;
  }
  setWorkingPage(value) {
    this.workingPage = value;
  }
}
