import axios from "axios";
import { observer } from "mobx-react";
import { useCallback } from "react";
import { useFetchTasks } from "../queries/useFetchTasks";
import { useStores } from "../state/Context";
import { nextTen } from "../utilities/function";

const PageDisplay = () => {
  const { TaskStore, DataStore } = useStores();
  const totalPage = Math.ceil(TaskStore.totalTask / TaskStore.tasksInPage);
  const baseNumber = Math.floor(TaskStore.workingPage / 10); //base number0
  const refetch = useFetchTasks();
  const onClickRight = async () => {
    if (nextTen(TaskStore.workingPage) > TaskStore.totalPage) {
      TaskStore.setWorkingPage(TaskStore.totalPage);
    } else {
      TaskStore.setWorkingPage(nextTen(TaskStore.workingPage));
    }
  };
  const onClick = async (number) => {
    TaskStore.setWorkingPage(number);
    console.log(TaskStore.workingPage);
    try {
      const data = await axios.get(
        `http://${process.env.REACT_APP_HOST_URL}/tasks?limit=${TaskStore.tasksInPage}&page=${number}`
      );
      console.log(data.data.rows);
      TaskStore.setTasks(data.data.rows);
    } catch (error) {
      console.error(error);
    }
  };
  const PageButton = useCallback(() => {
    let array = [];
    for (let i = 1; i < 10; i++) {
      if (baseNumber + i > totalPage) {
        break;
      }
      array.push(
        <div key={baseNumber + i} onClick={() => onClick(baseNumber + i)}>
          {baseNumber + i}
        </div>
      );
    }
    return array;
  }, [baseNumber, totalPage]);
  return (
    <div className="root">
      <div className="border">&lt;&lt;</div>
      <div className="border">&lt;</div>
      <PageButton></PageButton>
      <div className="border" onClick={onClickRight}>
        &gt;
      </div>
      <div className="border">&gt;&gt;</div>
    </div>
  );
};
export default observer(PageDisplay);
