import { useState, useRef, useEffect } from "react";
import Taskdisplay from "./Taskdisplay";
import Tagdisplay from "./Tagdisplay";
import axios from "axios";
import { useStores } from "../state/Context";
import { observer } from "mobx-react";
import { useFetchHashtags } from "../queries/useFetchHashtags";

const Hashtags = () => {
  const { TaskStore, DataStore } = useStores();
  const [hashtag, setHashtag] = useState("Hashtag");
  const all = useRef(0);
  const refetchAll = useFetchHashtags();

  const nameRefetch = async () => {
    try {
      const result = await axios.get(
        `http://${process.env.REACT_APP_HOST_URL}/hashtags/` +
          encodeURIComponent(hashtag)
      );
      TaskStore.setTasks(result.data.Tasks);
      all.current = 1;
    } catch (error) {
      console.log(error);
    }
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    nameRefetch();
  };
  const hashtagHandler = (e) => {
    setHashtag(e.target.value);
  };

  useEffect(() => {
    refetchAll();
  }, []);
  return (
    <div className="Center">
      <h1>Hashtag</h1>
      <button type="button" onClick={() => DataStore.setWorking("Tasks")}>
        Todo
      </button>
      <button type="button" onClick={() => refetchAll()}>
        Get All Hashtag
      </button>
      <form onSubmit={onSubmit}>
        <input
          type="text"
          placeholder="Hashtag"
          value={hashtag}
          onChange={hashtagHandler}
        ></input>
        <button type="submit">search</button>
      </form>
      {all.current === 0 && <Tagdisplay />}
      {all.current === 1 && <Taskdisplay />}
    </div>
  );
};

export default observer(Hashtags);
