import { useMutation } from "react-query";
import axios from "axios";
import { useStores } from "../state/Context";
import { observer } from "mobx-react";
import { toJS } from "mobx";
import { useFetchHashtags } from "../queries/useFetchHashtags";

const Tagdisplay = () => {
  const { HashtagStore } = useStores();
  const refetchAll = useFetchHashtags();
  const { mutate } = useMutation(
    ({ name }) => {
      return axios.delete(
        `http://${process.env.REACT_APP_HOST_URL}/hashtags/${name}`
      );
    },
    { onSuccess: () => refetchAll() }
  );
  const onDelete = async (name) => {
    mutate({ name: name });
  };
  return (
    <ul>
      {HashtagStore.length() === 0 && <h3>nothing</h3>}
      {toJS(HashtagStore.Hashtags).map((tag) => {
        return (
          <div key={tag.id}>
            <li>
              <div className="root">
                <div className="span">id</div>
                {tag.id}
                <div className="span">name</div>
                {tag.name}
                <div className="span">color</div>
                {tag.wordColor}
                <div className="span">backgroundColor</div>
                {tag.backgroundColor}
              </div>
            </li>
            <button onClick={() => onDelete(tag.name)}>delete</button>
          </div>
        );
      })}
    </ul>
  );
};
export default observer(Tagdisplay);
