import Taskdisplay from "./Taskdisplay";
import { useEffect, useState } from "react";
import { useFetchTasks } from "../queries/useFetchTasks";
import { observer } from "mobx-react";
import { useStores } from "../state/Context";
import PageDisplay from "./PageDisplay";
import axios from "axios";
const Tasks = () => {
  const { DataStore, TaskStore } = useStores();
  const refetchAll = useFetchTasks("");
  const [num, setNum] = useState(4);
  const refetchNear = useFetchTasks("nearness");
  const [display, setDisplay] = useState(true);
  useEffect(() => {
    refetchAll();
  }, []);
  const onSubmit = async (e) => {
    e.preventDefault();
    TaskStore.setTasksInPage(num);
    const data = await axios.get(
      `http://${process.env.REACT_APP_HOST_URL}/tasks?limit=${TaskStore.tasksInPage}`
    );
    TaskStore.setTasks(data.data.rows);
  };
  const onChange = (e) => {
    setNum(e.target.value);
  };
  return (
    <div className="Center">
      <h1>Todo</h1>
      <div className="root">
        <button
          type="button"
          onClick={() => {
            DataStore.setWorking("Hashtag");
            setDisplay(0);
          }}
        >
          Hashtag
        </button>
        {display && (
          <form onSubmit={onSubmit}>
            <div>
              {TaskStore.tasksInPage}/{TaskStore.totalTask} tasks are in one
              page
            </div>
            <input type="number" onChange={onChange} num={num}></input>
            <button className="small" type="submit">
              change
            </button>
          </form>
        )}
      </div>

      <div className="root">
        <button
          type="button"
          onClick={() => {
            refetchAll();
            setDisplay(true);
          }}
        >
          Get All Tasks
        </button>
        <button
          type="button"
          onClick={() => {
            refetchNear();
            setDisplay(false);
          }}
        >
          Get Nearness
        </button>
      </div>
      <Taskdisplay />
      {display && <PageDisplay />}
    </div>
  );
};
export default observer(Tasks);
