import axios from "axios";
import { observer } from "mobx-react";
import { useRef, useState } from "react";
import { useStores } from "../state/Context";
import moment from "moment";
import { useFetchTasks } from "../queries/useFetchTasks";

const Input = () => {
  const { DataStore } = useStores();
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [targetDate, setTargetDate] = useState(
    moment().add(1, "day").format("YYYY-MM-DD hh:mm")
  );
  const isChecked = useRef(0);

  const regex = /^([\d]{4}-[\d]{2}-[\d]{2} [\d]{2}:[\d]{2})$/;
  const isOk = useRef(true);
  const allRefetch = useFetchTasks();

  const onChangeTitle = (e) => {
    setTitle(e.target.value);
  };
  const onChangeDescription = (e) => {
    setDescription(e.target.value);
  };
  const onChangeTargetDate = (e) => {
    setTargetDate(e.target.value);
    //console.log(typeof moment(targetDate, "YYYY-MM-DD hh:mm", true)._d);
  };
  const onSubmit = async (e) => {
    e.preventDefault();
    if (title === "") {
      return alert("please type title");
    }
    if (description === "") {
      return alert("please type description");
    }
    if (targetDate !== "" && regex.test(targetDate) === false) {
      return alert("please use valid targetDate");
    }
    if (DataStore.id === 0) {
      isOk.current = await axios.post(
        `http://${process.env.REACT_APP_HOST_URL}/tasks/`,
        {
          title,
          description,
          clear: isChecked.current,
          targetDate,
        },
        {
          Accept: "application/json",
        }
      );
    }
    if (DataStore.id !== 0) {
      isOk.current = await axios.put(
        `http://${process.env.REACT_APP_HOST_URL}/tasks/` + DataStore.id,
        {
          id: DataStore.id,
          title,
          description,
          clear: isChecked.current,
          targetDate,
        },
        {
          Accept: "application/json",
        }
      );
    }
    console.dir(isOk.current.result);
    if (isOk.current.result === "failure") {
      alert("try again");
    } else {
      allRefetch();
    }
    DataStore.setId(0);
  };
  return (
    <div className="Center">
      <h1>
        {DataStore.id === 0 ? " Add new task" : `Edit task ${DataStore.id}`}
      </h1>
      <form onSubmit={onSubmit}>
        <div>
          <span>title</span>
          <input type="text" onChange={onChangeTitle} value={title}></input>
        </div>
        <div>
          <span>description</span>
          <input
            type="text"
            onChange={onChangeDescription}
            value={description}
          ></input>
        </div>
        <div>
          <span>targetDate 년-월-일 시각:분</span>
          <input
            type="text"
            placeholder="2022-02-08 07:30"
            onChange={onChangeTargetDate}
            value={targetDate}
          ></input>
        </div>
        <div>
          <span>clear</span>
          <input
            type="checkbox"
            onChange={() => {
              isChecked.current = 1 - isChecked.current;
            }}
          ></input>
        </div>
        <button type="submit">Submit</button>
        <button type="button" onClick={() => DataStore.setId(0)}>
          number reset
        </button>
      </form>
    </div>
  );
};
export default observer(Input);
