import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleHalfStroke } from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { useStores } from "../state/Context";
import { observer } from "mobx-react";
import { StyledDivHeader } from "../components/tags";

const Header = () => {
  const { DataStore } = useStores();
  let navigate = useNavigate();
  const onClick = () => navigate("/login");
  const onClickHeader = () => {
    console.log("1234");
    navigate("/");
  };

  const onLogout = async () => {
    DataStore.setLoggedin(false);
    await axios.get(`http://${process.env.REACT_APP_HOST_URL}/auth/logout`);
    navigate("/");
  };
  return (
    <StyledDivHeader className="Header__container">
      <div onClick={DataStore.setIsLight}>
        <FontAwesomeIcon icon={faCircleHalfStroke} size="5x" />
      </div>
      <h1 onClick={onClickHeader}>Todo List</h1>

      {DataStore.loggedIn ? (
        <button type="button" onClick={onLogout}>
          <h2>Logout</h2>
        </button>
      ) : (
        <button type="button" onClick={onClick}>
          <h2>Login</h2>
        </button>
      )}
    </StyledDivHeader>
  );
};
export default observer(Header);
