import { useEffect } from "react";
import axios from "axios";
import Header from "./pages/Header";
import "./App.css";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Home from "./pages/Home";
import Login from "./pages/Login";
import Register from "./pages/Register";
import TodoContainer from "./pages/TodoContainer.js";
import { observer } from "mobx-react";
import { useStores } from "./state/Context.jsx";
import { StyledDiv } from "./components/tags";

const App = () => {
  const { DataStore } = useStores();

  useEffect(() => {
    axios.defaults.withCredentials = true;
    const testing = async () => {
      try {
        await axios.get(`http://${process.env.REACT_APP_HOST_URL}/auth/check`);
        DataStore.setLoggedin(true);
      } catch (error) {
        DataStore.setLoggedin(false);
      }
    };
    testing();
  }, [DataStore.loggedIn, DataStore.setLoggedin]);
  return (
    <StyledDiv>
      <BrowserRouter>
        <Header />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/login" element={<Login />} />
          <Route path="/join" element={<Register />} />
          <Route path="/tasks" element={<TodoContainer />} />
        </Routes>
      </BrowserRouter>
    </StyledDiv>
  );
};

export default observer(App);
