import { useMutation } from "react-query";
import axios from "axios";
import { observer } from "mobx-react";
import { useStores } from "../state/Context";
import { useFetchTasks } from "../queries/useFetchTasks";

const Taskdisplay = () => {
  const { TaskStore, DataStore } = useStores();
  const refetchAll = useFetchTasks();
  const { mutate } = useMutation(
    ({ id }) => {
      return axios.delete(
        `http://${process.env.REACT_APP_HOST_URL}/tasks/${id}`
      );
    },
    { onSuccess: () => refetchAll() }
  );
  const onDelete = async (id) => {
    mutate({ id: id });
    console.log("1234");
  };
  return (
    <ul>
      {TaskStore.length() === 0 && <h3>nothing</h3>}
      {TaskStore.Tasks.map((task) => {
        return (
          <li key={task.id}>
            <div className="root">
              <div className="span">id</div>
              {task.id}
              <div className="span">title</div>
              {task.title}
              <div className="span">description</div>
              {task.description}
              <div className="span">{task.clear ? "clear" : ""}</div>

              <div className="span">targetDate</div>
              {new Date(task.targetDate).toString()}
            </div>
            <button onClick={() => DataStore.setId(task.id)}>수정</button>
            <button onClick={() => onDelete(task.id)}> 삭제 </button>
          </li>
        );
      })}
    </ul>
  );
};
export default observer(Taskdisplay);
