import { useQuery } from "react-query";
import axios from "axios";
import { useStores } from "../state/Context";

export const useFetchTasks = (id = "") => {
  const { TaskStore, DataStore } = useStores();
  const { refetch } = useQuery(
    ["tasks", id, TaskStore.tasksInPage, DataStore.workingPage],
    () => axios.get(`http://${process.env.REACT_APP_HOST_URL}/tasks/${id}`),
    {
      enabled: false,
      onSuccess: (data) => {
        console.log(data.data);
        TaskStore.setTotalTask(data.data.count);
        TaskStore.setTasks(data.data.rows);
      },
      onError: (data) => {
        if (data.response.data.error === "not loggedIn") {
          DataStore.setLoggedin(false);
        }
      },
    }
  );
  return refetch;
};
