import { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useMutation } from "react-query";
import { observer } from "mobx-react";
import { useStores } from "../state/Context";

const Login = () => {
  let navigator = useNavigate();
  const { DataStore } = useStores();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const emailHandler = (e) => {
    setEmail(e.target.value);
  };
  const passwordHandler = (e) => {
    setPassword(e.target.value);
  };

  const { mutate, status } = useMutation(
    ({ email, password }) => {
      return axios.post(`http://${process.env.REACT_APP_HOST_URL}/auth/login`, {
        email,
        password,
      });
    },
    {
      onSuccess: () => {
        DataStore.setLoggedin(true);
        navigator("/");
      },
      onError: () => {
        alert("please try again");
        setEmail("");
        setPassword("");
      },
      retry: false,
    }
  );
  const onSubmit = async (e) => {
    e.preventDefault();
    mutate({ email, password });
  };
  if (status === "loading") {
    return <h1>WAIT FOR LOGIN</h1>;
  }
  return (
    <div className="Container">
      <form onSubmit={onSubmit}>
        <span>email</span>
        <input type="email" value={email} onChange={emailHandler}></input>
        <br />
        <span>password</span>
        <input
          type="password"
          value={password}
          onChange={passwordHandler}
        ></input>
        <br />
        <button type="submit">sign in</button>
        <button
          type="button"
          onClick={() => {
            navigator("/join");
          }}
        >
          <span className="wordSize">Go To The Register Page</span>
        </button>
      </form>
    </div>
  );
};
export default observer(Login);
