import { useStores } from "../state/Context";
import { Color } from "../config/styled";
import styled from "styled-components";
import { observer } from "mobx-react";

const Div = styled.div`
  color: ${(props) => props.Color};
  background-color: ${(props) => props.backgroundColor};
`;
export const StyledDiv = observer(({ className = "", onClick, children }) => {
  const { DataStore } = useStores();
  return (
    <Div
      backgroundColor={Color[DataStore.isLight].BackgroundColor}
      Color={Color[DataStore.isLight].Color}
      onClick={onClick}
      className={className}
    >
      {children}
    </Div>
  );
});
export const StyledDivHeader = observer(
  ({ className = "", onClick, children }) => {
    const { DataStore } = useStores();
    return (
      <Div
        backgroundColor={Color[DataStore.isLight].HeaderBackgroundColor}
        Color={Color[DataStore.isLight].HeaderColor}
        onClick={onClick}
        className={className}
      >
        {children}
      </Div>
    );
  }
);
