import { action, makeObservable, observable } from "mobx";

export class DataStore {
  rootStore;
  // user = {};
  loggedIn = false;
  id = 0; //current Working
  working = "Tasks";
  isLight = "Light";
  userName = "";
  workingPage = 1;

  constructor(root) {
    makeObservable(this, {
      loggedIn: observable,
      id: observable,
      working: observable,
      isLight: observable,
      workingPage: observable,

      setWorkingPage: action,
      nextPage: action,
      postPage: action,
      setIsLight: action.bound,
      setLoggedin: action,
      setId: action,
      setWorking: action,
    });

    this.rootStore = root;

    // this.user = new Loggedin(false);
  }

  setLoggedin(value) {
    // this.user = new Loggedin(value);
    this.loggedIn = value;
  }
  setId(value) {
    console.log(this);
    this.id = value;
  }
  setWorking(value) {
    this.working = value;
  }
  setIsLight() {
    if (this.isLight === "Light") {
      this.isLight = "Dark";
    } else {
      this.isLight = "Light";
    }
  }

  setWorkingPage(value) {
    this.workingPage = value;
  }
  nextPage() {
    this.workingPage++;
  }
  postPage() {
    this.workingPage--;
  }
}
